import React, { useState } from 'react';
import { Button, Container } from "react-bootstrap";

const trade = [
    " 1 ",
    " 1 ~ 2 ",
    " 2 ~ 5 ",
    " 5 ~ 10 ",
    " 10  ",
]

const Section05 = ({ onNext, onPrevious }) => {
    const [activeIndex, setActiveIndex] = useState(null); //    
    const [currentSection, setCurrentSection] = useState(5); //   
    const handlePrevious = () => {
        if (currentSection > 1) {
            setCurrentSection(currentSection - 1); //   
            onPrevious(); //   ""    
        }
    };
    const handleClick = (index) => {
        setActiveIndex(index); //    
    };



    return (
        <Container className="my-5 py-5">
            <div className="d-flex justify-content-center gap-3">
                <div className="paybackBar active"></div>
                <div className="paybackBar active"></div>
                <div className="paybackBar active"></div>
                <div className="paybackBar active"></div>
            </div>
            <div className="text-center mt-4">
                <h2>
                        <span className="color-primary"></span>?
                </h2>
                <p className="text-muted">  .</p>
            </div>
            <div className="d-flex flex-column gap-4">
                {
                    trade.map((item, index) => (
                        <div
                            key={index}
                            className={`tradeItem w-100 border text-center py-3 rounded-4 color-secondary ${activeIndex === index ? 'active' : ''}`}
                            onClick={() => handleClick(index)} //   handleClick 
                        >
                            <h3 className="mb-0">{item}</h3>
                        </div>
                    ))
                }
            </div>
            <div className="paybackbtncustop d-flex justify-content-center gap-3 mt-4 mb-5 pt-5">
                <Button
                    type="button"
                    className="w-100 rounded-4 py-3 fw-bold"
                    onClick={handlePrevious} //     ""  
                >
                    
                </Button>
                <Button
                    type="button"
                    className="w-100 rounded-4 fw-bold"
                    onClick={onNext}
                    disabled={activeIndex === null} // activeIndex null  
                >
                    
                </Button>
            </div>
        </Container>
    );
};

export default Section05;
