import React, {useState} from 'react';
import {Button, Container} from "react-bootstrap";
import image from "../../../../assets/images/section09banner.png"

const Section09 = ({onPrevious, onNext}) => {
    const [currentSection, setCurrentSection] = useState(9); //   
    const handlePrevious = () => {
        if (currentSection > 1) {
            setCurrentSection(currentSection - 1); //   
            onPrevious(); //   ""    
        }
    };
    return (
        <Container className="my-5 py-5">
            <div className="text-center mt-4">
                <h2>
                     ,  <span className="color-primary"> </span> !
                </h2>
                <p className="text-muted">   .</p>
            </div>
            <div>
                <img src={image} width={500} className="m-auto d-block w-mo-100" alt=""/>
            </div>
            <div>
                <p className=" mt-5 text-muted text-center">
                    *   .
                </p>
            </div>
            <div className="paybackbtncustop d-flex justify-content-center gap-3 mt-4 mb-5 pt-5">
                <Button
                    type="button"
                    className="w-100 rounded-4 py-3 fw-bold"
                    onClick={handlePrevious} //     ""  
                >
                    
                </Button>
                <Button
                    type="button"
                    className="w-100 rounded-4 fw-bold"
                    onClick={onNext}
                >
                    
                </Button>
            </div>
        </Container>
    );
};

export default Section09;
