import React, { useState } from 'react';
import { Button, Container, FormControl, InputGroup } from 'react-bootstrap';
import closeIcon from "../../../../assets/images/icon/closeIcon.png"

const Section04 = ({ onNext, onPrevious }) => {
    const [pageNumber, setPageNumber] = useState(''); // 
    const [currentSection, setCurrentSection] = useState(4); //   
    const handlePrevious = () => {
        if (currentSection > 1) {
            setCurrentSection(currentSection - 1); //   
            onPrevious(); //   ""    
        }
    };//    

    //  , 1 ~ 125    
    const handleInputChange = (e) => {
        const value = e.target.value;

        //    (  ) && 1 ~ 125   
        if (/^\d*$/.test(value) && (value === '' || (parseInt(value) >= 1 ))) {
            setPageNumber(value);
        }
    };

    const handleClose = () => {
        setPageNumber(''); // "X"     
    };

    return (
        <Container className="my-5 py-5">
            <div className="d-flex justify-content-center gap-3">
                <div className="paybackBar active"></div>
                <div className="paybackBar active"></div>
                <div className="paybackBar active"></div>
                <div className="paybackBar"></div>
            </div>
            <div className="text-center mt-4">
                <h2>
                    <span className="color-primary"></span>  ?
                </h2>
                <p className="text-muted">Bybit    .</p>
            </div>
            <div className="position-relative pb-5 mb-5">
                <InputGroup className="w-auto position-relative">
                    <FormControl
                        type="text"
                        value={pageNumber}
                        onChange={handleInputChange}
                        placeholder="  ."
                        aria-label="Page number"
                        className="border-0 border-bottom px-0 rounded-0"
                    />
                    <div className="d-flex align-items-center position-absolute end-0 top-0 bottom-0 m-auto gap-3 h-fitcontent">
                        <span className="fw-bold fs-5 text-muted">USDT</span>
                        <div
                            onClick={handleClose}
                            className="bg-transparent cursor-pointer cursor-pointer"
                        >
                            <img src={closeIcon} width={15} alt=""/>
                        </div>
                    </div>
                </InputGroup>

            </div>
            <div className="paybackbtncustop d-flex justify-content-center gap-3 mt-4 mb-5 pt-5">
                <Button
                    type="button"
                    className="w-100 rounded-4 py-3 fw-bold"
                    onClick={handlePrevious} //     ""  
                >
                    
                </Button>
                <Button
                    type="button"
                    className="w-100 rounded-4 fw-bold"
                    onClick={onNext}
                    disabled={pageNumber === '' || parseInt(pageNumber) < 1} //  1 ~ 125   
                >
                    
                </Button>
            </div>
        </Container>
    );
};

export default Section04;
