import React from 'react';
import {Swiper, SwiperSlide} from "swiper/react";
import {Autoplay, Pagination} from "swiper/modules";
import SwiperCore from "swiper";
import {Container} from "react-bootstrap";

const BannerSwiper = () => {
    SwiperCore.use([Autoplay]);
    return (
        <Container>
            <div className="bannerSwiper position-relative rounded-4 overflow-hidden">
                <Swiper
                    autoplay={{
                        delay: 2000,  //     
                        disableOnInteraction: false  //    autoplay 
                    }}
                    pagination={{
                        type: 'fraction',
                    }}
                    loop={true}
                    modules={[Pagination]}
                    className="mySwiper"
                >
                    <SwiperSlide style={{height: "300px"}} className="bg-secondary"> </SwiperSlide>
                    <SwiperSlide style={{height: "300px"}} className="bg-secondary"> </SwiperSlide>
                    <SwiperSlide style={{height: "300px"}} className="bg-secondary"> </SwiperSlide>
                </Swiper>
            </div>
        </Container>
    );
};

export default BannerSwiper;
